.social {
  margin-bottom: 22px;
  padding: 20px 12px; //    12
  display: grid;
  grid-template-columns: repeat(auto-fit, 24px);
  gap: 32px;

  @media (min-width: $tablet-width) {
    margin-bottom: 0;
    grid-template-columns: repeat(auto-fit, 30px);
    gap: 24px;
  }
  justify-content: center; //      
  list-style-type: none; //     .                         
  @media (min-width: $tablet-width) {
    padding: 0; //     social        page-footer
  }
}

.social__item {
  margin: 0; //         
}

.social__link {
  color: $special_grey-3; //            .
  width: 24px;
  height: 24px;
  display: grid;
  place-content: center;

  @media (min-width: $tablet-width) {
    width: 30px;
    height: 30px;
    svg {
      transform: scale(1.43);
    }
  }

  &:hover:not(.social__link--facebook) path {
    fill: $color-primary;
  }
  &--facebook:hover path{
    stroke: $color-primary;
  }

  &:active:not(.social__link--facebook) path {
    fill: fade-out($color-primary, 0.7);
  }
  &--facebook:active path{
    stroke: fade-out($color-primary, 0.7);
  }
}
